import { Tag } from "@prisma/client";
import { json, LoaderFunction, MetaFunction } from "@remix-run/node";
import {
  Links,
  LiveReload,
  Meta,
  Outlet,
  Scripts,
  ScrollRestoration,
  useFetcher,
  useLoaderData,
  useSearchParams,
} from "@remix-run/react";
import { useEffect } from "react";
import Layout from "./components/layout";
import Sidebar from "./components/sidebar";
import styles from "./styles/app.css";
import { prisma } from "./utils/prisma.server";
import { useTransition } from "@remix-run/react";
import { withSentry } from "@sentry/remix";
import { getStreamInfo } from "./lib/getStreamInfo.server";
import useUrlState from "./hooks/useUrlState";

export const meta: MetaFunction = () => ({
  charset: "utf-8",
  title: "HasanHub",
  viewport: "width=device-width,initial-scale=1",
  description: "The HasanAbi Clips Industrial Complex App",
  keywords: "hasanabi, hasanhub, hasan piker, streamer, youtube, clips, twitch",
  "msapplication-tileColor": "#da532c",
  "theme-color": "#ffffff",
  "yandex-verification": "45afda70569d2af8",
});

export function links() {
  return [
    { rel: "stylesheet", href: styles },
    { rel: "icon", href: "/favicon.ico" },
    {
      rel: "apple-touch-icon",
      size: "180x180x",
      href: "/apple-touch-icon.png",
    },
    {
      rel: "icon",
      type: "image/png",
      sizes: "32x32",
      href: "/favicon-32x32.png",
    },
    {
      rel: "icon",
      type: "image/png",
      sizes: "16x16",
      href: "/favicon-16x16.png",
    },
    {
      rel: "mask-icon",
      href: "/safari-pinned-tab.svg",
      color: "#5bbad5",
    },
    {
      rel: "manifest",
      href: "site.webmanifest",
    },
    {
      rel: "preconnect",
      href: "	https://i.ytimg.com",
    },
    {
      rel: "dns-prefetch",
      href: "	https://i.ytimg.com",
    },
  ];
}

export const loader: LoaderFunction = async ({ params, request }) => {
  const [tags, [streamInfo, schedule]] = await Promise.all([
    prisma.$queryRaw`
      SELECT t.*, sum(v.views) AS view_count
      FROM Tag t
        JOIN TagVideo tv ON tv.tagId = t.id
        JOIN Video v ON tv.videoId = v.id
      GROUP BY t.id
      ORDER BY view_count DESC
    `,
    getStreamInfo(),
  ]);

  await prisma.$disconnect();

  return json({
    tags,
    streamInfo,
    schedule,
  });
};

function App() {
  const { tags, streamInfo, schedule } = useLoaderData();
  const { durations, tagSlugs } = useUrlState();

  return (
    <html lang="en">
      <head>
        <Meta />
        <Links />
      </head>
      <body>
        <Layout streamInfo={streamInfo} streamSchedule={schedule}>
          <Sidebar
            tags={tags}
            activeTagSlugs={tagSlugs}
            durationFilter={durations}
          />
          <Outlet />
        </Layout>
        <ScrollRestoration />
        <Scripts />
        <LiveReload />

        <script
          defer
          src="/stats/js/script.js"
          data-api="/stats/api/event"
          data-domain="hasanhub.com"
        />
      </body>
    </html>
  );
}

export default withSentry(App);
